export default {
	data() {
		return {
			isfirst:true,
			headerPosition:"fixed",
			headerTop:null,
			statusTop:null,
			showHeader:true,
			//个人信息,
			user:{
				username:'游客',
				face:'/static/img/logo.jpg',
				signature:'ID:01',
				integral:0,
				balance:0,
				envelope:0
			},
			// 订单类型
			orderList:[
				{text:'待付款',icon:"fukuan"},
				{text:'待发货',icon:"fahuo"},
				{text:'待收货',icon:"shouhuo"},
				{text:'待评价',icon:"pingjia"},
				{text:'退换货',icon:"tuihuo"}
			],
			// 工具栏列表
			mytoolbarList:[
				{url:'../../user/keep/keep',text:'我的收藏',img:'/static/img/user/point.png'},
				{url:'../../user/coupon/coupon',text:'优惠券',img:'/static/img/user/quan.png'}, 
				{url:'',text:'新客豪礼',img:'/static/img/user/renw.png'},
				{url:'',text:'领红包',img:'/static/img/user/momey.png'},
				
				{url:'../../user/address/address',text:'收货地址',img:'/static/img/user/addr.png'},
				{url:'',text:'账户安全',img:'/static/img/user/security.png'},
				{url:'',text:'银行卡',img:'/static/img/user/bank.png'},
				{url:'',text:'抽奖',img:'/static/img/user/choujiang.png'},
				// {text:'客服',img:'/static/img/user/kefu.png'},
				// {text:'签到',img:'/static/img/user/mingxi.png'}
				
			]
		}
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
		this.headerTop = e.scrollTop>=0?null:0;
		this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
	},
	onLoad() {
		this.statusHeight = 0;




	},
	onReady(){
		//此处，演示,每次页面初次渲染都把登录状态重置
		uni.setStorage({
			key: 'UserInfo',
			data: false,
			success: function () {
			},
			fail:function(e){
			}
		});
	},
	onShow(){
		uni.getStorage({
			key: 'UserInfo',
			success: (res)=>{
				if(!res.data){
					if(this.isfirst){
						//this.toLogin();
					}
					return ;
				}
				this.user = res.data;
			},
			fail:(e)=>{
				//this.toLogin(); 
			}
		});
	},
	methods: {
		//消息列表
		toOrderList(index){
			uni.setStorageSync('tbIndex',index);
			uni.navigateTo({url:'../../user/order_list/order_list?tbIndex='+index}) 
		},
		toSetting(){
			uni.navigateTo({
				url:'../../user/setting/setting'
			})
		},
		toLogin(){
			uni.showToast({title: '请登录',icon:"none"});
			uni.navigateTo({
				url:'../../login/login'
			})
			this.isfirst = false;
		},
		isLogin(){
			//实际应用中,用户登录状态应该用token等方法去维持登录状态.
			const value = uni.getStorageSync('UserInfo');
			if (value) {
				return true;
			}
			return false
		},
		toDeposit(){
			uni.navigateTo({
				url:'../../user/deposit/deposit'
			})
		},
	}
} 
